h1,h2,h3,p {
  text-align: center;
}
.heading {
  margin-bottom: 3em;
}
.boardRow {
  display: flex;
  flex-flow: row nowrap;
}
.square {
  width: 2.6vw;
  height: 2.6vw;
  line-height: 2.6vw;
  text-align: center;
  padding: 0;
  font-size: 1.6vw;
  border: 1px solid #C0C0C0;
}
.square:focus {outline: 0;}
.kbd-navigation .square:focus {
  background: #ddd;
}
.hightLight {
  font-weight: 900;
}
.winning {
  color:black;
}
.scoreRow {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  text-align: center;

}
.computerScore, .humanScore {
  padding: 1em;

}
.board_and_info {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
}
.info {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
  width: 25vw;

}
.score {
  padding: 0;
}
.score > h2 {
  margin: 0;
}

.status h2 {
  margin: 0;
}
.scoreStat {
  font-weight: bold;
}
.statistics h2 {
  margin: 0;
}
/* making the heart beats */
#heart {
  height: 10px;
  width: 10px;
  background-color: rgb(223, 74, 74);
  display: inline-block;
  transform: rotate(-45deg);
  margin: 0 10px;
  position: relative;
  top: 0;
  animation-name: beat;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}
#heart:before,
#heart:after {
  content: "";
  background-color: rgb(223, 74, 74);
  border-radius: 50%;
  height: 10px;
  position: absolute;
  width: 10px;
}
#heart:before {
  top: -5px;
  left: 0;
}

#heart:after {
  left: 5px;
  top: 0;
}
@keyframes beat {
  0% {
    transform: scale(1) rotate(-45deg);
  }
  50% {
    transform: scale(0.6) rotate(-45deg);
  }
}
@media only screen and (max-width: 768px) {
  .total_game {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
  }
  .board_and_info {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
  }
  .square {
    width: 2.5vw;
    height: 2.5vw;
    line-height: 2.5vw;
    text-align: center;
    font-size: 1.6vw;
  }
  .info {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: center;
    width: 80vw;
    padding: 0;
  } 
  .computerScore, .humanScore {
    padding-top: 0em;
  }
  .heading {
    margin-bottom: 0em;
  }
  .statistics {
    margin-top: 1em;
  }
}